* {
  box-sizing: border-box;
}

.clearfix:after {
  content: ".";
  clear: both;
  visibility: hidden;
  height: 0;
  line-height: 0;
  display: block;
}

.clear {
  clear: both;
}

@font-face {
  font-family: Open Sans;
  src: url("OpenSans-Regular.77a7692b.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Open Sans;
  src: url("OpenSans-Light.f665824e.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

body {
  color: #f4f4f4cc;
  word-spacing: 2px;
  background-color: #0e1414;
  flex-direction: column;
  font: 300 16px / 1.5 Open Sans, sans-serif;
  display: flex;
}

img {
  object-fit: cover;
  width: 100%;
  height: auto;
}

h3 {
  color: #fff;
  letter-spacing: 1.1px;
  font-size: 1rem;
  font-weight: 500;
}

p {
  max-width: 90%;
  margin-bottom: 1rem;
}

a {
  color: inherit;
  text-decoration: none;
}

.mainComponents, .blogPosts, #bottomComponent {
  justify-content: center;
  display: flex;
}

.mainComponents {
  flex-direction: row;
  height: 50%;
}

#bottomComponent {
  flex-direction: column;
}

.componentSections {
  width: 29%;
}

.centered {
  text-align: center;
  color: #f4f4f4;
  font-size: 2.3rem;
}

section h1 {
  text-transform: uppercase;
  color: #f4f4f4e6;
  margin-top: 0;
  margin-bottom: .3125rem;
  font-weight: 300;
}

#topComponent h1 {
  word-spacing: 12px;
}

.redText, .readMore {
  text-transform: uppercase;
  font-weight: 500;
}

.readMore {
  color: #fff;
  border-bottom: 1px solid red;
  padding-bottom: .5rem;
}

.redText {
  color: red;
}

.bold {
  color: #f4f4f4;
  font-weight: 500;
}

.topHeading {
  border-bottom: 1px solid #fff;
  padding: 0 .3125rem .3125rem;
}

section * {
  padding: 1.7%;
}

.redLined {
  border-bottom: 3px solid red;
  transition: border-bottom .5s, transform .5s;
}

.redLined:hover {
  border-bottom-color: #0000;
  transform: scale(1.1);
}

.blogPosts .componentSections {
  transition: transform .2s, background-color .3s, border .3s, box-shadow .3s;
}

.blogPosts .componentSections:hover {
  background-color: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 5px;
  transform: scale(1.02);
  box-shadow: 0 0 10px #ffffff4d;
}

.readMore {
  transition: border-color .5s, background-color .5s;
}

.readMore:hover {
  background-color: #fff3;
  border: 1px solid #0000;
  border-radius: 3px;
}

.modaled {
  transition: box-shadow .3s, transform .3s;
}

.modaled:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px #ffffff80;
}

.modal {
  z-index: 9999;
  opacity: 0;
  background-color: #000000e6;
  width: 100%;
  height: 100%;
  transition: opacity .3s ease-in-out;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal.show {
  opacity: 1;
  display: block;
}

.modal-content {
  width: auto;
  max-width: 80%;
  max-height: 80vh;
  margin: 5% auto auto;
  display: block;
}

.close {
  color: #fff;
  font-size: 40px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  top: 15px;
  right: 35px;
}

.close:hover, .close:focus {
  color: #bbb;
  cursor: pointer;
  text-decoration: none;
}

#topComponent {
  grid-gap: 1.25rem;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 5%;
  display: grid;
}

#topComponent .componentSections {
  width: 100%;
  max-width: 100%;
}

#topComponent .componentSections:first-child {
  grid-area: 1 / 1 / span 2 / span 1;
}

#topComponent .componentSections:nth-child(2) {
  grid-area: 1 / 2 / span 2 / span 1;
  width: 100%;
}

#topComponent .smallerText {
  grid-area: 2 / 3 / span 1 / span 1;
}

#topComponent .biggerText {
  grid-area: 1 / 3 / span 1 / span 1;
}

#topComponent img, #topComponent h1, #topComponent p, #topComponent span, #topComponent .bold {
  width: 100%;
  max-width: 100%;
}

@media screen and (width <= 850px) {
  #topComponent {
    grid-gap: 20px;
    grid-template-rows: auto auto;
    grid-template-columns: 1fr 1fr;
    margin: 10%;
    display: grid;
  }

  #topComponent .componentSections:first-child {
    grid-area: 1 / 1 / span 1 / span 1;
  }

  #topComponent .componentSections:nth-child(2) {
    grid-area: 1 / 2 / span 1 / span 1;
  }

  #topComponent .textContainer {
    grid-gap: 20px;
    grid-area: 2 / 1 / span 1 / span 2;
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  #topComponent .biggerText {
    grid-area: 2 / 1 / span 1 / span 1;
  }

  #topComponent .smallerText {
    grid-area: 2 / 2 / span 1 / span 1;
  }

  #topComponent .textContainer .biggerText, #topComponent .textContainer .smallerText {
    width: 100%;
    max-width: 100%;
  }

  #bottomComponent .blogPosts {
    flex-direction: column;
    align-items: center;
  }

  .blogPosts .componentSections {
    flex-direction: column;
    align-items: center;
    width: 65%;
    display: flex;
  }

  .blogPosts span {
    align-self: flex-start;
    margin: 1% 5%;
  }

  h3 {
    text-align: center;
  }

  .blogPosts .componentSections p {
    text-align: justify;
    width: 110%;
  }

  .blogPosts a {
    width: 85%;
  }
}

@media screen and (width <= 650px) {
  #topComponent .componentSections:first-child {
    grid-area: 5 / 1 / span 1 / span 2;
  }

  #topComponent .componentSections:nth-child(2) {
    grid-area: 7 / 2 / span 1 / span 1;
  }

  #topComponent .smallerText {
    grid-area: 7 / 1 / span 1 / span 1;
  }

  #topComponent .biggerText {
    grid-area: 1 / 1 / span 4 / span 2;
  }

  .biggerText h1 {
    text-align: center;
  }

  .centered {
    align-self: center;
    width: 65%;
    font-size: 1.8rem;
  }
}

